.app_wrapper {
  width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-areas:
  "header header"
  "nav content";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 1fr 5fr;
  grid-gap: 10px;
}

.app_wrapper_content {
  grid-area: content;
  background-color: #f1f1f1;
  padding: 10px;
}